<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  created() {
    // eslint-disable-next-line no-underscore-dangle
    window._this = this;
    this.$store.dispatch({ type: 'setting/getConfig' });
    if (window.IframeMsgHelper) {
      const { config } = this.$store.state;
      window.addEventListener('keyup', (event) => {
        const { triggerKeys, platform } = config;
        if (
          event.altKey === !!triggerKeys[platform].altKey
          && event.ctrlKey === !!triggerKeys[platform].ctrlKey
          && event.metaKey === !!triggerKeys[platform].metaKey
          && event.shiftKey === !!triggerKeys[platform].shiftKey
          && event.key === triggerKeys[platform].key
        ) {
          window.iframeMessager.emit('hideFrame');
        }
      });

      window.iframeMessager = new window.IframeMsgHelper(window.top, '__cbdui')
        .on('home', () => {
          this.$router.replace('/');
        });
      window.iframeMessager.emit('load');
    }

    const route = window.sessionStorage.getItem('__cbdui_route_path');
    if (route) {
      this.$router.replace(route);
    }
  },
  watch: {
    '$route.path': function (value) {
      window.sessionStorage.setItem('__cbdui_route_path', value);
    },
  },
};
</script>

<style lang="less">
  html, body, #app {
    height: 100%;
    overflow: hidden;
  }

  .el-menu-item.is-active {
    background-color: #ecf5ff;
  }

  :focus {
    outline: -webkit-focus-ring-color auto 0;
  }
</style>
