import { getTemplates } from '@/services/templateServices';

export default ({
  namespaced: true,
  state: {
    activeMenu: 'page',
    templates: [],
  },
  mutations: {
    update(state, { type, ...otherState }) {
      Object.entries(otherState)
        .forEach(([key, value]) => {
          state[key] = value;
        });
    },
  },
  actions: {
    getTemplates({ commit }) {
      getTemplates()
        .then((result) => {
          if (result.success) {
            commit({
              type: 'update',
              templates: result.data.map((d) => ({
                ...d,
                random: Math.random(),
              })),
            });
          }
        });
    },
  },
  modules: {},
});
