import {
  get, post, put, del,
  interceptor,
  requestUrlPrefix,
  responseIntCode,
  responseSuccessSign,
} from '@ali-whale/fetch-web';
import { Message } from 'element-ui';


/* 移除所有请求拦截器 */
// interceptor.request.clear();
/* 所有的请求url添加前缀"/api" */
interceptor.request.use(requestUrlPrefix('/__cbdui/api'));

/* 将string类型的errCode转换为number */
interceptor.response.use(responseIntCode());

/* 当errCode = 0 时，你的响应结果将增加一个 success: true 字段 */
interceptor.response.use(responseSuccessSign({ code: 0 }));

interceptor.response.use(({ result }) => {
  if (result.errCode) {
    Message.error(result.errMsg);
  }
});

export {
  get, post, put, del,
};
