export default ({
  namespaced: true,
  state: {
    version: '-',
    templatePage: '',
    templateModel: '',
    templateComponent: '',
  },
  mutations: {
    update(state, { type, ...otherState }) {
      Object.entries(otherState)
        .forEach(([key, value]) => {
          state[key] = value;
        });
    },
  },
  actions: {
  },
  modules: {},
});
