import { get, post } from '@/utils/fetch';

export const getTemplates = () => get('/getTemplates');

export const syncTemplates = () => get('/syncTemplates');

export const mkdir = (path) => post('/mkdir', { path });

export const previewTemplateFiles = ({ name, template, path }) => get('/previewTemplateFiles', {
  name,
  template,
  path,
});

export const createFromTemplate = ({ name, template, path }) => post('/create', {
  name,
  template,
  path,
});

export const checkTemplatePath = ({ name, template, path }) => get('/checkPath', {
  name,
  template,
  path,
});
