import { getConfig, setConfig } from '../services/settingServices';

export default ({
  namespaced: true,
  state: {
    triggerKeys: {
      win: {
        ctrlKey: true,
        shiftKey: false,
        altKey: true,
        key: 'x',
      },
      mac: {
        ctrlKey: true,
        shiftKey: false,
        altKey: false,
        metaKey: true,
        key: 'x',
      },
    },
  },
  mutations: {
    update(state, { type, ...otherState }) {
      Object.entries(otherState)
        .forEach(([key, value]) => {
          state[key] = value;
        });
    },
  },
  actions: {
    getConfig(context) {
      getConfig()
        .then((result) => {
          if (result.success) {
            context.commit({
              type: 'update',
              ...result.data,
            });
            context.commit({
              type: 'config/update',
              ...result.data,
            }, { root: true });
            if (window.iframeMessager) {
              window.iframeMessager.emit('config', result.data);
            }
          }
        });
    },
    setConfig({ dispatch, commit }, { type, ...newState } = {}) {
      commit({
        type: 'update',
        ...newState,
      });
      setConfig({ ...newState })
        .then((result) => {
          if (result.success) {
            dispatch('getConfig');
          }
        });
    },
  },
  modules: {},
});
